body {
  margin: 0 auto;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

img {
  max-width: 100%;
  display: block;
}

button {
  cursor: pointer;
}

.search-form {
  width: 100%;
  background-color: #339291;
  justify-content: center;
  gap: 10px;
  padding: 20px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form input {
  border-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  padding: 8px 16px;
}

.search-form button {
  border-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.search-form button:hover {
  color: #fff;
  background-color: #81c8c6;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 72px;
  padding: 20px 16px;
  display: flex;
}

.photo-card {
  flex-direction: column;
  flex-basis: calc(25% - 15px);
  display: flex;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2), 0 6px 20px rgba(0, 0, 0, .19);
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info {
  justify-content: space-between;
  padding: 8px 16px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  row-gap: 4px;
  margin: 0;
  display: flex;
}

.load-more {
  color: #fff;
  background-color: #339291;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  margin: 0 auto;
  padding: 8px 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.load-more.is-hidden {
  display: none;
}

.load-more:hover {
  color: #339291;
  background-color: #81c8c6;
}

